.blog h1 {
  margin-bottom: 0.7rem; }

.blog__hero {
  min-height: 300px;
  height: 60vh;
  width: 100%;
  margin: 0; }
  .blog__hero img {
    margin-bottom: 0;
    object-fit: cover; }

.blog__info {
  padding: 1.5rem 1.25rem;
  width: 100%;
  max-width: 768px;
  margin: 0 auto; }
  .blog__info h1 {
    margin-bottom: 0.66rem; }
  .blog__info h3 {
    margin-bottom: 0; }

.blog__body {
  width: 100%;
  padding: 0 1.25rem;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: center; }
  .blog__body a {
    padding-bottom: 1.5rem; }
  .blog__body :last-child {
    margin-bottom: 0; }
  .blog__body h1, .blog__body h2, .blog__body h3, .blog__body h4, .blog__body h5, .blog__body h6, .blog__body p {
    font-weight: normal; }
  .blog__body p {
    color: inherit; }
  .blog__body ul {
    list-style: initial; }
  .blog__body ul, .blog__body ol {
    margin-left: 1.25rem;
    margin-bottom: 1.25rem;
    padding-left: 1.45rem; }

.blog__footer {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1.5rem 1.25rem;
  width: 100%;
  max-width: 800px;
  margin: 0 auto; }
  .blog__footer h2 {
    margin-bottom: 0; }
  .blog__footer a {
    display: flex;
    justify-content: space-between;
    align-items: center; }
    .blog__footer a svg {
      width: 20px; }

@media (min-width: 768px) {
  .blog {
    display: flex;
    flex-direction: column; }
  .blog__body {
    max-width: 800px;
    padding: 0 2rem; }
    .blog__body span {
      width: 100%;
      margin: 1.5rem auto; }
    .blog__body ul, .blog__body ol {
      margin-left: 1.5rem;
      margin-bottom: 1.5rem; }
  .blog__hero {
    min-height: 600px;
    height: 75vh; }
    .blog__hero img {
      min-width: 100%; }
  .blog__info {
    text-align: center;
    padding: 2rem 0; }
    .blog__info h1 {
      max-width: 500px;
      margin: 0 auto 0.66rem auto; }
  .blog__footer {
    padding: 2.25rem; } }

@media (min-width: 1440px) {
  .blog__hero {
    height: 70vh; }
  .blog__info {
    padding: 3rem 0; }
  .blog__footer {
    padding: 2rem 2rem 3rem 2rem; } }
